.footer {
    padding: 30px 20px;
    color: var(--color-white);
    text-align: center;
    background-color: var(--color-blue-400);
    margin: auto 0 0 0;
}

.footer__text {
    display: flex;
    justify-content: center;
    gap: 7px;
    align-items: center;
}

.footer__icon {
    width: 16px;
}

.footer__link {
    color: var(--color-blue-200);
    text-decoration: none;
    transition: 0.1s color ease;
}

.footer__link:hover {
    color: var(--color-blue-300);
}

.footer__icon {
    transition: .3s;
}

.footer__icon:hover {
    fill: rgb(255, 71, 134);
    cursor: pointer;
}