.trip-page {
    padding: 60px 20px;
}

.trip {
    display: flex;
    flex-wrap: wrap;
    gap: 20px 50px;
    width: 100%;
    max-width: var(--container-width);
    margin: 0 auto;
}

.trip__img {
    width: 400px;
    height: 400px;
    object-fit: cover;
}

.trip__content {
    display: flex;
    flex: 1 1 400px;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    padding-top: 20px;
}

.trip__description {
    opacity: 0.7;
}

.trip__button {
    max-width: 200px;
}

.trip__button--gray {
    background: gray;
}